@import url('https://fonts.googleapis.com/css2?family=Poppins:wght@400;500;600;700&display=swap');

body {
  margin: 0;
  padding: 0;
  font-family: 'Poppins', sans-serif;
  background-color: black;
}

.buttons {
    background-color: #d3d3d3;
    border-width: 2px;
    border-style: solid;
    border-color: rgb(216, 216, 216) rgb(209, 209, 209) rgb(186, 186, 186);
    border-image: initial;
    height: 60px;
    width: 100px;
}

.container {
  max-width: 1100px;
  margin: 0 auto;
  padding: 0 15px;
  display: flex;
  justify-content: space-between;
  align-items: center;
  height: 100%;
}

.navbar {
  height: 80px;
  background-color: #a7a7a7;
  position: absolute;
  width: 100%;
  z-index: 1;
}

.logo {
  /* Add styles for logo */
  height: 10px;
  width: 100px;
  margin-top: -85px;
  position:absolute;
  
}
.CompanyName{
    font-size: 25px;
    font-weight: 400;
    color: #2f234f;
    text-decoration: none;
    position:relative;
   margin-left: 100px;
    flex-direction: horizontal;
    user-select: auto;
}

.nav-elements {
  /* Add styles for nav-elements */
}

.nav-elements ul {
  display: flex;
  justify-content: space-between;
  list-style-type: none;
  margin: 0; /* Remove default margin */
  padding: 0; /* Remove default padding */
}

.nav-elements ul li {
  margin-right: 20px; /* Adjust the margin for desktop */
}

/* Media query for screens smaller than 768px (typical mobile screens) */
@media (max-width: 768px) {
  .container {
    flex-direction: horizontal; /* Stack elements vertically on smaller screens */
    align-items: flex-start; /* Align items to the start */
    
  }
  .CompanyName{
    font-size: 10px;
    font-weight: 400;
    color: #2f234f;
    text-decoration: none;
    position:relative;
    user-select: none; /* Disable text selection */
    flex-direction: horizontal;
    margin-left: 0px;
    
}

  .nav-elements ul {
    flex-direction: row; /* Display list items horizontally on mobile */
    justify-content: space-evenly; /* Adjust spacing for mobile */
    width: 100%; /* Make sure the list takes full width on mobile */
    margin-top: 10px; /* Add spacing between logo and nav elements */
    margin-right: 10px;
  }

  .nav-elements ul li {
    margin-right: 8px; /* Remove margin for mobile */
    margin-bottom: 0px; /* Add spacing between list items */
  }
}

.nav-elements ul a {
  font-size: 16px;
  font-weight: 400;
  color: #2f234f;
  text-decoration: none;
  user-select: none; /* Disable text selection */
}

.nav-elements ul a.active {
  color: #574c4c;
  font-weight: 500;
  position: relative;
}

.nav-elements ul a.active::after {
  content: '';
  position: absolute;
  bottom: -4px;
  left: 0;
  width: 100%;
  height: 2px;
  background-color: #574c4c;
}

