@import '~video-react/dist/video-react.css';

.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: flex-start; /* Align items to the start (top) of the container */
  justify-content: flex-start; /* Align items to the start (top) of the container */
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

.OverlayPanel {
  position: fixed;
  top: 80px; /* Adjust the top position based on your navbar height */
  right: 20px; /* Adjust the right position as needed */
  background-color: rgb(121, 121, 121);
  padding: 0px;
  border: 0px solid #ccc;
  z-index: 2; /* Ensure the overlay panel is above other content */

}



.video-container {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  overflow: hidden;
  z-index: 0;
}

#background-video {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  object-fit: cover;
  z-index: 0;
}

video {
  width: 100%;
  height: 100%;
  object-fit: cover;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
