@import url(https://fonts.googleapis.com/css2?family=Poppins:wght@400;500;600;700&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: flex-start; /* Align items to the start (top) of the container */
  justify-content: flex-start; /* Align items to the start (top) of the container */
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

.OverlayPanel {
  position: fixed;
  top: 80px; /* Adjust the top position based on your navbar height */
  right: 20px; /* Adjust the right position as needed */
  background-color: rgb(121, 121, 121);
  padding: 0px;
  border: 0px solid #ccc;
  z-index: 2; /* Ensure the overlay panel is above other content */

}



.video-container {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  overflow: hidden;
  z-index: 0;
}

#background-video {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  object-fit: cover;
  z-index: 0;
}

video {
  width: 100%;
  height: 100%;
  object-fit: cover;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

body {
  margin: 0;
  padding: 0;
  font-family: 'Poppins', sans-serif;
  background-color: black;
}

.buttons {
    background-color: #d3d3d3;
    border-width: 2px;
    border-style: solid;
    border-color: rgb(216, 216, 216) rgb(209, 209, 209) rgb(186, 186, 186);
    border-image: initial;
    height: 60px;
    width: 100px;
}

.container {
  max-width: 1100px;
  margin: 0 auto;
  padding: 0 15px;
  display: flex;
  justify-content: space-between;
  align-items: center;
  height: 100%;
}

.navbar {
  height: 80px;
  background-color: #a7a7a7;
  position: absolute;
  width: 100%;
  z-index: 1;
}

.logo {
  /* Add styles for logo */
  height: 10px;
  width: 100px;
  margin-top: -85px;
  position:absolute;
  
}
.CompanyName{
    font-size: 25px;
    font-weight: 400;
    color: #2f234f;
    text-decoration: none;
    position:relative;
   margin-left: 100px;
    flex-direction: horizontal;
    -webkit-user-select: auto;
            user-select: auto;
}

.nav-elements {
  /* Add styles for nav-elements */
}

.nav-elements ul {
  display: flex;
  justify-content: space-between;
  list-style-type: none;
  margin: 0; /* Remove default margin */
  padding: 0; /* Remove default padding */
}

.nav-elements ul li {
  margin-right: 20px; /* Adjust the margin for desktop */
}

/* Media query for screens smaller than 768px (typical mobile screens) */
@media (max-width: 768px) {
  .container {
    flex-direction: horizontal; /* Stack elements vertically on smaller screens */
    align-items: flex-start; /* Align items to the start */
    
  }
  .CompanyName{
    font-size: 10px;
    font-weight: 400;
    color: #2f234f;
    text-decoration: none;
    position:relative;
    -webkit-user-select: none;
            user-select: none; /* Disable text selection */
    flex-direction: horizontal;
    margin-left: 0px;
    
}

  .nav-elements ul {
    flex-direction: row; /* Display list items horizontally on mobile */
    justify-content: space-evenly; /* Adjust spacing for mobile */
    width: 100%; /* Make sure the list takes full width on mobile */
    margin-top: 10px; /* Add spacing between logo and nav elements */
    margin-right: 10px;
  }

  .nav-elements ul li {
    margin-right: 8px; /* Remove margin for mobile */
    margin-bottom: 0px; /* Add spacing between list items */
  }
}

.nav-elements ul a {
  font-size: 16px;
  font-weight: 400;
  color: #2f234f;
  text-decoration: none;
  -webkit-user-select: none;
          user-select: none; /* Disable text selection */
}

.nav-elements ul a.active {
  color: #574c4c;
  font-weight: 500;
  position: relative;
}

.nav-elements ul a.active::after {
  content: '';
  position: absolute;
  bottom: -4px;
  left: 0;
  width: 100%;
  height: 2px;
  background-color: #574c4c;
}


